<template>
  <div class="pipeline">
    <h1>This is an about page</h1>
    <button v-on:click="getCrumb"></button>
    <p v-bind:token="token"></p>
  </div>
</template>
<script>
import axios from "axios";

export default {
  data:{
    token: ""
  },
  methods:{
    getCrumb: function () {
      axios.get('http://localhost:8080/pipeline/crumb')
        .then((result) => {
          console.log(result)
          this.token = result.data
        })
    }
  }
}
</script>
